import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/page/welcome'
import IviewNav from '@/page/iviewNav'
import IviewForm from '@/page/iviewForm'
import IviewModal from '@/page/iviewModal'
import IviewUpload from '@/components/uploadImage'
import Yar from '@/page/yar'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  mode:'hash',    //打包需要修改
  routes: [
    {
      path: '/',
      name: 'welcome',
      component:Welcome
    },
    {
      path: '/form',
      name: 'IviewForm',
      component:IviewForm
    },
    {
      path: '/modal',
      name: 'Modal',
      component:IviewModal
    },
    {
      path: '/upload',
      name: 'Upload',
      component:IviewUpload
    },
    {
      path: '/yar',
      name: 'Yar',
      component:Yar
    },
  ]
})



